export default {
	data() {
		return {
			imgShow: false,
			index: 0,
			showBtn: false,
			screenHeight: 0,
			imgLength: 0,
			providerList: [],
			data: [],
			detailDec: ""
		}
	},
	onLoad(e) {





		this.screenHeight = uni.getSystemInfoSync().windowHeight;
		this.detailDec = e.data;
		let data = JSON.parse(decodeURIComponent(e.data));
		this.imgLength = data.img_num;
		this.data.push(data.img_src);
		uni.setNavigationBarTitle({
			title: "1/" + this.imgLength
		});
		// 获取分享通道
		uni.getProvider({
			service: "share",
			success: (e) => {
				let data = []
				for (let i = 0; i < e.provider.length; i++) {
					switch (e.provider[i]) {
						case 'weixin':
							data.push({
								name: '分享到微信好友',
								id: 'weixin'
							})
							data.push({
								name: '分享到微信朋友圈',
								id: 'weixin',
								type: 'WXSenceTimeline'
							})
							break;
						case 'qq':
							data.push({
								name: '分享到QQ',
								id: 'qq'
							})
							break;
						default:
							break;
					}
				}
				this.providerList = data;
			},
			fail: (e) => {
				console.log("获取登录通道失败", e);
			}
		});
	},
	onShareAppMessage() {
		return {
			title: '欢迎使用uni-app看图模板',
			path: '/pages/detail/detail?data=' + this.detailDec,
			imageUrl: this.data[this.index]
		}
	},
	onNavigationBarButtonTap(e) {
		if (e.index === 0) {







































		}
	},
	methods: {
		download() {
			uni.downloadFile({
				url: this.data[this.index],
				success: (e) => {
					uni.saveImageToPhotosAlbum({
						filePath: e.tempFilePath,
						success: () => {
							uni.showToast({
								icon: 'none',
								title: '已保存到手机相册'
							})
						},
						fail: () => {
							uni.showToast({
								icon: 'none',
								title: '保存到手机相册失败'
							})
						}
					});
				},
				fail: (e) => {
					uni.showModal({
						content: '下载失败，' + e.errMsg,
						showCancel: false
					})
				}
			})
		},
		collect() {
			uni.showToast({
				icon: 'none',
				title: '点击收藏按钮'
			})
		},








































		swpierChange(e) {
			this.index = e.detail.current;
			uni.setNavigationBarTitle({
				title: e.detail.current + 1 + '/' + this.imgLength
			})
		},
		preImg(index) {
			if (this.imgShow) { //防止点击过快导致重复调用 
				return;
			}
			this.imgShow = true;
			setTimeout(() => {
				this.imgShow = false;
			}, 1000)
			setTimeout(() => {
				uni.previewImage({
					current: this.data[index],
					urls: this.data
				})
			}, 150)
		}
	}
}
